@import url(https://fonts.googleapis.com/css2?family=Roboto&display=swap);
body {
  width: 100%;
  margin: 0;
  padding: 0;
  font-family: 'Roboto', sans-serif;
}

* {
  box-sizing: border-box;
}
.styles_app__1LNKg {
    width: 100%;
    height: 100vh;
    margin: 0;
}

.styles_header__2qSeF {
    width: 100%;
    height: 10rem;
    padding-top: 2rem;
}

.styles_header__2qSeF h5 {
    float: left;
    margin-left: 10px;
}

.styles_miniCircle__32B7c {
    width: 50px;
    height: 50px;
    border-radius: 50%;
    background: linear-gradient(360deg, rgba(132, 114, 213, 0.3), rgba(61, 130, 223, 0.2)), url(/static/media/bg.d17b956a.jpg);
    padding: 10px;
    float: left;
}

.styles_miniCircle__32B7c img {
    width: 45px;
}

.styles_center__3asL7 {
    width: 100%;
    height: auto;
    display: block;
    padding: 2rem 10rem;
}

.styles_circle__3wW1U {
    width: 300px;
    height: 300px;
    background: #000;
    border-radius: 50%;
    margin: 0 auto;
    background: linear-gradient(360deg, rgba(132, 114, 213, 0.8), rgba(61, 130, 223, 0.8)), url(/static/media/bg.d17b956a.jpg);
    padding: 5rem;
    text-align: center;
    color: white;
    background-size: cover;
    position: relative;
}

.styles_error__3IHTi {
    background: crimson;
}

.styles_circle__3wW1U:hover {
    -webkit-transform: scale(1.1);
            transform: scale(1.1);
}

.styles_circle__3wW1U svg {
    font-size: 4rem;
    margin-bottom: 1rem;
}

.styles_circle__3wW1U h3 {
    font-size: 1.5rem;
}

.styles_center__3asL7 h1 {
    font-size: 2rem;
    font-weight: bold;
    color: royalblue;
}

.styles_center__3asL7 h4 {
    font-size: 1.5rem;
    display: block;
    font-weight: bold;
    margin-bottom: 2rem;
}

.styles_tab__3hLDA {
    width: 100%;
    height: 5rem;
    border-top: 1px solid gray;
    margin-top: -7vh;
    position: absolute;
}

.styles_square__2sN_1 {
    width: 50%;
    height: 5rem;
    background: whitesmoke;
    float: left;
    text-align: center;
    padding: 1.5rem;
    color: black;
}

.styles_form__1dqCA {
    width: 100%;
    height: auto;
    display: block;
    padding: 2rem;
}

.styles_formContainer__3_KHJ {
    padding: .5rem;
}

.styles_formContainer__3_KHJ h3 {
    font-size: 1.5rem;
    color: royalblue;
    font-weight: bold;
    margin-bottom: 1rem;
}






